import axios from "axios";

function getOrderDetail(orderNo) {
  return axios.get(`http://localhost:82/order/orderDetail?orderNo=${orderNo}`);
}

function getOrderList(during) {
  return axios.get(`http://localhost:82/order/orderList?&during=${during}`);
}

function getUserPoint() {
  return axios.get(`http://localhost:82/order/getUserPoint`);
}

function insertOrder(order) {
  return axios.post(`http://localhost:82/order/insertOrder`, order);
}

function cancelOrder(orderDetail){
  return axios.post(`http://localhost:82/order/cancelOrder`, orderDetail);
}


/* 
    카트
*/
function getCartList() {
  return axios.get(`http://localhost:82/cart/cartList`);
}

function insertCart(cart){
  return axios.post(`http://localhost:82/cart/insertCart`,cart);
}

function deleteCart(cart) {
  return axios.post(`http://localhost:82/cart/deleteCart`,cart);
}

function updateIsNotify(cart) {
  return axios.post(`http://localhost:82/cart/updateIsNotify`,cart);
}

function updateIsAutoPurchase(cart) {
  return axios.post(`http://localhost:82/cart/updateIsAutoPurchase`,cart);
}

function getOptionList(productDetailNo){
  return axios.get(`http://localhost:82/cart/getOptionList?productDetailNo=${productDetailNo}`);
}

function updateOption(cart){
  return axios.post(`http://localhost:82/cart/updateOption`,cart);
}


export default {
  getOrderDetail,
  getOrderList,
  getUserPoint,
  insertOrder,
  cancelOrder,
  getCartList,
  insertCart,
  deleteCart,
  updateIsNotify,
  updateIsAutoPurchase,
  getOptionList,
  updateOption
};
