<template>
  <transition name="modal">
    <div class="modal-mask">
      <div class="modal-wrapper">
        <div class="modal-container">
          <div class="">
            <h3 class="mt-7 mb-15">{{ ModalMesage }}</h3>
            <div class="mt-10"></div>

            <v-row class="mx-auto mt-15">
              <v-col cos="1"></v-col>
              <v-col cos="2">
                <ParticleEffectButton
                  class=""
                  :visible.sync="btnOps.visible"
                  :animating.sync="btnOps.animating"
                  :options="btnOps"
                >
                  <span>{{ BtnText }}</span>
                </ParticleEffectButton>
              </v-col>
              <v-col cos="1"></v-col>
            </v-row>
          </div>
          <div v-if="!isFirst" text class="mt-15 mb-5">
            <p style="text-align-center" v-html="ModalDetailMessage"></p>
          </div>
          <button
            v-if="!isFirst"
            class="modal-default-button btn btn-sm btn-info"
            @click="$emit('close')"
          >
            닫기
          </button>
        </div>
      </div>
    </div>
  </transition>

  <!-- <v-card-actions class="justify-center">
              <v-btn text @click="dialog.value = false">Close</v-btn>
            </v-card-actions> -->

  <!-- https://github.com/dreambo8563/vue-particle-effect-buttons -->

  <!-- <h2>animating:{{ btnOps.animating }}</h2>
    <h2>visible:{{ btnOps.visible }}</h2>
    <v-btn btn color="primary" @click="btnOps.visible = !btnOps.visible">확인</v-btn> -->
</template>
<script>
import ParticleEffectButton from "vue-particle-effect-buttons";

export default {
  props: ["BtnBeforeText", "BtnAfterText", "ModalMesage", "ModalDetailMessage"],
  data() {
    return {
      modalText: "modal",
      BtnText: "LV.1",
      isFirst: true,
      btnOps: {
        type: "triangle",
        style: "stroke",
        size: 6,
        particlesAmountCoefficient: 3,
        oscillationCoefficient: 5,
        duration: 400,

        onComplete: () => {
          // console.log("complete");
          this.BtnText = this.BtnAfterText;
          if (this.isFirst) {
            this.btnOps.visible = true;
            this.btnOps.animating = true;
            this.isFirst = false;
            // this.$emit("close");
          }
          return;
        },
        onBegin: () => {
          // console.log("begin");
        },

        visible: true,
        animating: false,
      },
    };
  },
  components: {
    ParticleEffectButton,
  },
  created() {
    this.BtnText = this.BtnBeforeText;
  },
};
</script>

